.nav {
  margin-left: 22px;

  @include vp-1199 {
    padding-top: 3px;
    margin-left: 28px;
  }

  @include vp-767 {
    margin: 0;
    padding-top: 0;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  width: 461px;

  @include vp-1199 {
    column-gap: 10px;
    width: 421px;
  }

  @include vp-767 {
    width: auto;
    display: grid;
    grid-template-columns: repeat(2, 130px);
    grid-template-rows: repeat(3, auto);
    grid-column-gap: 30px;
    grid-row-gap: 16px;
    margin-bottom: 35px;
  }
}

.nav__item {
  &--services {
    @include vp-767 {
      grid-area: 2 / 1 / 3 / 2;
    }
  }

  &--subs {
    @include vp-767 {
      grid-area: 3 / 1 / 4 / 2;
    }
  }

  &--actions {
    @include vp-767 {
      grid-area: 1 / 2 / 2 / 3;
    }
  }

  &--trainers {
    @include vp-767 {
      grid-area: 2 / 2 / 3 / 3;
    }
  }

  &--schedule {
    @include vp-767 {
      grid-area: 3 / 2 / 4 / 3;
    }
  }
}

.nav__link {
  color: $text-light;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  position: relative;
  padding: 5px 10px;
  border: 1px solid transparent;

  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 10px;
    right: 10px;
    height: 1px;
    background-color: transparent;
  }

  @include vp-767 {
    font-size: 16px;
    margin-left: -10px;
  }

  @include hover {
    &::after {
      background-color: $color-default-white;
    }

    @include active {
      color: $text-light;

      &::after {
        background-color: transparent;
      }
    }
  }

  @include focus {
    outline: none;
    border: 1px solid $color-default-white;
  }

  &--disabled {
    opacity: 0.4;
    pointer-events: none;
  }
}
