.page-header {
  flex: 1;
  padding-top: 30px;
  padding-bottom: 51px;
  background: #0041ca $gradient-desktop;

  @include vp-1199 {
    padding-top: 35px;
    padding-bottom: 82px;
    background: #0041ca $gradient-tablet;
  }

  @include vp-767 {
    padding-top: 15px;
    padding-bottom: 78px;
    background: #0041ca $gradient-mobile;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  @include vp-1199 {
    margin-bottom: 18px;
    align-items: center;
  }

  @include vp-767 {
    margin-bottom: 68px;
  }
}

.page-header__col {
  &--logo {
    flex-shrink: 0;
  }

  &--tel {
    display: flex;
    flex-wrap: wrap;
    max-width: 70%;
    justify-content: flex-end;
  }
}

.page-header__tel {
  color: $text-light;
  font-family: $ff-alumniSans;
  font-size: 26px;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.78px;
  max-width: 154px;
  position: relative;
  z-index: 1;
  margin-left: 30px;

  @include vp-1199 {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 0.48px;
    max-width: 138px;
  }

  @include vp-767 {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.4px;
    max-width: 115px;
  }

  @include hover-focus {
    outline: none;
    opacity: 0.8;

    @include active {
      opacity: 0.3;
    }
  }
}
