// Colors
// Text
$text-dark: #102654;
$text-light: #ffffff;
$text-action: #103aae;
$text-action-gradient: linear-gradient(129deg, #d10a42 0%, #0041ca 100%);
$text-form: #ffffff;
$text-error: #ff121f;

// Icons
$icon-light: #ffffff;
$icon-dark: #102654;

// Buttons/Links
$stroke-light: #ffffff;
$stroke-dark: #102654;
$fill-gradient: linear-gradient(90deg, #ed0233 0%, #003eb7 100%);
$fill-tabs-gradient: linear-gradient(151deg, #ed0233 0%, #003eb7 100%);
$fill-action: #103aae;

// Default
$color-default-black: #000000;
$color-transparent: rgba(255, 255, 255, 0);
$color-default-white: #ffffff;
$color-grey: #f8f9fc;
$color-red: #ed0233;
$color-dark: #102654;
$color-darker: #030a1a;
$gradient-desktop: conic-gradient(from 151deg at 16.03% 39.06%, #003d85 20deg, #b80027 295deg, #003d85);
$gradient-tablet: conic-gradient(from 155deg at 20.7% 35.74%, #003d85 20deg, #b80027 295deg, #003d85);
$gradient-mobile: conic-gradient(from 167deg at 35.31% 34.22%, #003d85 20deg, #b80027 295deg, #003d85);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

// $color-neon-blue: #2c39f2;
// $color-torch-red: #ff1553;

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default: 16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble: "Rouble", "Arial", sans-serif;
$ff-robotoCondensed: "RobotoCondensed", "Arial", sans-serif;
$ff-alumniSans: "AlumniSans", "Arial", sans-serif;

$ff-placeholder: $ff-robotoCondensed;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

//      $tf-default,     
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-768: 768px;
$vp-1200: 1200px;
$vp-1366: 1366px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
